<template>
    <div class="section">
        <div class="item">
            <div class="title"> Popup</div>
            <div class="sub_title">
                @param width (ex: 500px or 50%)
            </div>
            <div class="sub_title">
                @param closeBtn  falseX width 
            </div>
            <div class="sub_title">
                @event close 
            </div>
            <Button text="" @click.native="popupOpen = 'sample'" />
        </div>

        <transition name="fade">
            <Popup
                v-if="popupOpen === 'sample'"
                :width="width"
                :close-btn="!closeBtnProps"
                @close="popupOpen = ''"
            >
                <template #content>
                    <div class="content">
                        <div class="title"></div>
                        <div class="btn">
                            <Button
                                @click.native="popupOpen = ''"
                                text=""
                            />
                            <Button
                                @click.native="popupOpen = ''"
                                :main-color="'gray'"
                                text=""
                            />
                        </div>
                    </div>
                </template>
            </Popup>
        </transition>

        <div class="controller">
            <div class="controller_title"></div>
            <pre class="code">{{ code }}</pre>

            <div class="item">
                <label class="controller_label"> width</label>
                <input
                    :disabled="!widthProps"
                    class="input"
                    type="text"
                    v-model.lazy="width"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="widthProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="widthProps"
                    :value="false"
                    @change="changeProps('width')"
                />
                <label> ("560px")</label>
            </div>

            <div class="item">
                <label class="controller_label"> closeBtn</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="closeBtnProps"
                    :value="true"
                />
                <label> (false)</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="closeBtnProps"
                    :value="false"
                />
                <label></label>
            </div>
        </div>
    </div>
</template>

<script>
import Button from "@/components/common/button/Button";
import Popup from "@/components/common/popup/Popup";
export default {
    name: "PopupPage",
    components: { Button, Popup },
    computed: {
        code() {
            return `
<Popup
    v-if="popupOpen === 'sample'"
    ${this.widthProps ? `:width="${this.width}"` : ""}
    ${this.closeBtnProps ? `:close-btn="false"` : ""}
    @close="popupOpen = ''"
/>`;
        }
    },
    data() {
        return {
            popupOpen: "",
            width: "300px",

            // 
            widthProps: true,
            closeBtnProps: false
        };
    },
    methods: {
        // 
        changeProps(target) {
            this[target] = undefined;
        }
    }
};
</script>

