<template>
    <div class="section">
        <div class="item">
            <div class="title"> Marquee</div>
            <div class="sub_title">@param content ()</div>
            <div class="sub_title">@param duration ()</div>
            <div class="sub_title">@param stop hover</div>
            <Marquee
                :content="content"
                :step="step"
                :duration="duration"
                :stop="stopProps"
            />
        </div>

        <div class="controller">
            <div class="controller_title"></div>
            <pre class="code">{{ code }}</pre>

            <div class="item">
                <label class="controller_label"> content</label>
                <input
                    class="input content_input"
                    type="text"
                    v-model.lazy="content[0]"
                />
                <input
                    class="input content_input"
                    type="text"
                    v-model.lazy="content[1]"
                />
                <input
                    class="input content_input"
                    type="text"
                    v-model.lazy="content[2]"
                />
                <input
                    class="input content_input"
                    type="text"
                    v-model.lazy="content[3]"
                />
                <input
                    class="input content_input"
                    type="text"
                    v-model.lazy="content[4]"
                />
            </div>

            <div class="item">
                <label class="controller_label"> step</label>
                <input
                    :disabled="!stepProps"
                    class="input"
                    type="number"
                    v-model.lazy.number="step"
                    @keydown="keydownNumber"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="stepProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="stepProps"
                    :value="false"
                    @change="changeProps('step')"
                />
                <label> (100)</label>
            </div>

            <div class="item">
                <label class="controller_label"> duration</label>
                <input
                    :disabled="!durationProps"
                    class="input"
                    type="number"
                    v-model.lazy.number="duration"
                    @keydown="keydownNumber"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="durationProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="durationProps"
                    :value="false"
                    @change="changeProps('duration')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> stop</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="stopProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="stopProps"
                    :value="false"
                />
                <label></label>
            </div>
        </div>
    </div>
</template>

<script>
import Marquee from "@/components/common/marquee/Marquee";
export default {
    name: "PopupPage",
    components: { Marquee },
    data() {
        return {
            content: [
                "1. Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut perferendis incidunt porro aperiam atque, tempora quod fugiat repellat magnam esse architecto numquam omnis harum cum inventore non voluptatum asperiores animi?",
                "2. Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut perferendis incidunt porro aperiam atque, tempora quod fugiat repellat magnam esse architecto numquam omnis harum cum inventore non voluptatum asperiores animi?",
                "3. Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut perferendis incidunt porro aperiam atque, tempora quod fugiat repellat magnam esse architecto numquam omnis harum cum inventore non voluptatum asperiores animi?"
            ],
            step: 50,
            duration: 0,

            // 
            stepProps: true,
            durationProps: false,
            stopProps: true
        };
    },
    computed: {
        // 
        code() {
            return `
<Marquee
    :content="[${this.content}]"
    ${this.stepProps ? `:step="${this.step}"` : ""}
    ${this.durationProps ? `:duration="${this.duration}"` : ""}
    ${this.stopProps ? "stop" : ""} 
/>`;
        }
    },
    methods: {
        // 
        keydownNumber(e) {
            //  - + . e
            const keycode = [69, 107, 109, 110, 187, 189, 190];
            if (keycode.includes(e.keyCode)) {
                e.preventDefault();
                return;
            }
        },
        changeProps(target) {
            this[target] = undefined;
        }
    }
};
</script>

<style scoped lang="scss">
.section {
    width: 100%;

    .controller .input.content_input {
        text-align: left !important;
        width: 100% !important;
        margin: 8px 0 !important;
    }
}
</style>
