<template>
    <div class="section">
        <div class="item">
            <div class="title"> NumberAnimation</div>
            <div class="sub_title">@param duration  ()</div>
            <div class="sub_title">@param finalValue  ()</div>
            <NumberAnimation :duration="duration" :final-value="finalValue" />
        </div>
        <div class="controller">
            <div class="controller_title"></div>
            <pre class="code">{{ code }}</pre>

            <div class="item">
                <label class="controller_label"> duration</label>
                <input
                    :disabled="!durationProps"
                    type="number"
                    class="input"
                    v-model.number="duration"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="durationProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="durationProps"
                    :value="false"
                    @change="changeProps('duration')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> finalValue</label>
                <input
                    type="number"
                    class="input"
                    v-model.number.lazy="finalValue"
                />
            </div>
        </div>
    </div>
</template>

<script>
import NumberAnimation from "@/components/common/numberAnimation/NumberAnimation";
export default {
    name: "NumberAnimationPage",
    components: { NumberAnimation },
    data() {
        return {
            //
            duration: 2000,
            finalValue: 45849412,

            durationProps: true
        };
    },
    computed: {
        // 
        code() {
            return `
<NumberAnimation
    ${this.durationProps ? `:duration="${this.duration}"` : ""}
    :final-value="${this.finalValue}"
/>`;
        }
    },
    methods: {
        // 
        changeProps(target) {
            this[target] = undefined;
        }
    }
};
</script>

