<template>
    <div class="section">
        <div class="item">
            <div class="title"> BaseInput</div>
            <div class="sub_title">
                @param label  ()
            </div>
            <div class="sub_title">
                @param placeholder  ( input placeholder)
            </div>
            <div class="sub_title">
                @param is-valid.sync  ()
            </div>
            <div class="sub_title">
                @param rules  ()
            </div>
            <div class="sub_title">
                @param error-message  ()
            </div>
            <div class="sub_title">
                @param type  ( input type)
            </div>
            <div class="sub_title">
                @param disabled  ( input disabled)
            </div>
            <div class="sub_title">
                @param not-required  ()
            </div>
            <div class="sub_title">
                @param multi-line  (labelinput)
            </div>
            <div class="sub_title">
                @param clear-btn  (false)
            </div>
            <div class="sub_title">
                @event onBlur 
            </div>
            <div class="sub_title">
                @event onKeyup 
            </div>
            <BaseInput
                v-model="sample.value"
                :label="sample.label"
                :placeholder="sample.placeholder"
                :is-valid.sync="sample.isValid"
                :rules="sample.rules"
                :error-message="sample.errorMessage"
                :type="sample.type"
                :disabled="disabledProps"
                :not-required="notRequiredProps"
                :multi-line="multiLineProps"
                :clear-btn="!clearBtnProps"
            />
        </div>

        <!--  -->
        <div class="controller">
            <div class="controller_title"></div>
            <pre class="code">{{ code }}</pre>

            <div class="item">
                <label class="controller_label"> label</label>
                <input
                    :disabled="!labelProps"
                    class="input"
                    type="type"
                    v-model="sample.label"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="labelProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="labelProps"
                    :value="false"
                    @change="changeProps('label')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> placeholder</label>
                <input class="input" type="type" v-model="sample.placeholder" />
                <input
                    type="radio"
                    class="radio"
                    v-model="placeholderProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="placeholderProps"
                    :value="false"
                    @change="changeProps('placeholder')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> errorMessage</label>
                <input
                    :disabled="!errorMessageProps"
                    class="input"
                    type="type"
                    v-model="sample.errorMessage"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="errorMessageProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="errorMessageProps"
                    :value="false"
                    @change="changeProps('errorMessage')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> type</label>
                <input
                    :disabled="!typeProps"
                    class="input"
                    type="type"
                    v-model="sample.type"
                />
                <input
                    type="radio"
                    class="radio"
                    v-model="typeProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="typeProps"
                    :value="false"
                    @change="changeProps('type')"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> disabled</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="disabledProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="disabledProps"
                    :value="false"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> notRequired</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="notRequiredProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="notRequiredProps"
                    :value="false"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> multiLine</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="multiLineProps"
                    :value="true"
                />
                <label></label>
                <input
                    type="radio"
                    class="radio"
                    v-model="multiLineProps"
                    :value="false"
                />
                <label></label>
            </div>

            <div class="item">
                <label class="controller_label"> clearBtn</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="clearBtnProps"
                    :value="true"
                />
                <label> (false)</label>
                <input
                    type="radio"
                    class="radio"
                    v-model="clearBtnProps"
                    :value="false"
                />
                <label></label>
            </div>
        </div>

        <!-- Email  -->
        <div class="item">
            <div class="title"> (rules)</div>
            <pre class="code">{{ codeEmail }}</pre>
            <BaseInput
                label=""
                placeholder=""
                v-model="mail.value"
                :is-valid.sync="mail.isValid"
                :rules="mail.rules"
            />
            <Button text="" :disabled="!mail.isValid" />
        </div>

        <!-- compar  -->
        <div class="item">
            <div class="title">
                 (onBlur, onKeyup, errorMessage)
            </div>
            <pre class="code">{{ codeCompare1 }}</pre>
            <pre class="code">{{ codeCompare2 }}</pre>
            <pre class="code">{{ confirmPasswordHandlerFunciton }}</pre>
            <BaseInput
                label=""
                placeholder=""
                type="password"
                v-model="password.value"
                :is-valid.sync="password.isValid"
                :rules="password.rules"
                @onKeyup="confirmPasswordHandler"
            />
            <BaseInput
                label=""
                placeholder=""
                type="password"
                v-model="confirmPassword.value"
                :is-valid.sync="confirmPassword.isValid"
                :rules="confirmPassword.rules"
                :error-message="confirmPassword.errorMessage"
                @onBlur="confirmPasswordHandler"
                @onKeyup="confirmPasswordHandler"
            />
            <Button
                text=""
                :disabled="!password.isValid || !confirmPassword.isValid"
            />
        </div>

        <!--   -->
        <div class="item">
            <div class="title"> (isRequire)</div>
            <pre class="code">{{ codeNotRequired }}</pre>
            <BaseInput
                label="Line"
                placeholder="Line"
                v-model="line.value"
                :is-valid.sync="line.isValid"
                not-required
            />
            <Button
                text=""
                :disabled="!line.isValid || line.value.trim() === ''"
            />
        </div>

        <!--   -->
        <div class="item">
            <div class="title"> (type numberrules)</div>
            <pre class="code">{{ codeNumber }}</pre>
            <BaseInput
                label=""
                placeholder=""
                type="number"
                v-model="amount.value"
                :is-valid.sync="amount.isValid"
                :rules="amount.rules"
                :error-message="amount.errorMessage"
                @onBlur="amountHandler(amount.value)"
                @onKeyup="amountHandler(amount.value)"
            />
            <Button text="" :disabled="!amount.isValid" />
        </div>

        <!--   -->
        <div class="item">
            <div class="title">labelinput (multiLine)</div>
            <pre class="code">{{ codeMultiLine }}</pre>
            <BaseInput
                label=""
                placeholder=""
                v-model="account.value"
                :is-valid.sync="account.isValid"
                :rules="account.rules"
                multi-line
            />
        </div>

        <!--   -->
        <div class="item">
            <div class="title"> (clearBtn)</div>
            <pre class="code">{{ codeClearBtn }}</pre>
            <BaseInput
                label="FB"
                v-model="fb.value"
                :is-valid.sync="fb.isValid"
                not-required
                :clear-btn="false"
            />
        </div>

        <div class="item">
            <div class="title"></div>
            <BaseInput2
                label="/"
                placeholder=""
                v-model="account2.value"
                :is-valid.sync="account2.isValid"
                :rules="account2.rules"
            />
            <BaseInput2
                label=""
                placeholder=""
                type="password"
                v-model="password2.value"
                :is-valid.sync="password2.isValid"
                :rules="password2.rules"
            />
        </div>
    </div>
</template>

<script>
import BaseInput from "@/components/common/baseInput/BaseInput.vue";
import BaseInput2 from "@/components/common/baseInput/BaseInput2.vue";
import Button from "@/components/common/button/Button.vue";
export default {
    name: "BaseInputPage",
    components: {
        BaseInput,
        BaseInput2,
        Button
    },
    data() {
        return {
            mail: {
                value: "",
                isValid: false,
                rules: {
                    limit: "mail"
                }
            },
            account: {
                value: "",
                isValid: false,
                rules: {
                    min: 6,
                    max: 20,
                    limit: "enAndNumber"
                }
            },
            password: {
                value: "",
                isValid: false,
                rules: {
                    min: 6,
                    max: 20,
                    limit: "enAndNumber"
                }
            },
            confirmPassword: {
                value: "",
                isValid: false,
                rules: {
                    min: 6,
                    max: 20,
                    limit: "enAndNumber"
                },
                errorMessage: ""
            },
            line: {
                value: "",
                isValid: false
            },
            amount: {
                value: "",
                isValid: false,
                rules: {
                    limit: "number"
                },
                errorMessage: ""
            },
            fb: {
                value: "",
                isValid: false
            },
            account2: {
                value: "",
                isValid: false,
                rules: {
                    min: 6,
                    max: 20,
                    limit: "enAndNumber"
                }
            },
            password2: {
                value: "",
                isValid: false,
                rules: {
                    min: 6,
                    max: 20,
                    limit: "enAndNumber"
                }
            },

            // 
            sample: {
                value: "",
                label: "",
                placeholder: "..",
                isValid: false,
                rules: {},
                errorMessage: "",
                type: ""
            },
            labelProps: true,
            placeholderProps: true,
            errorMessageProps: false,
            typeProps: false,
            disabledProps: false,
            notRequiredProps: false,
            multiLineProps: false,
            clearBtnProps: false
        };
    },
    computed: {
        code() {
            return `
<BaseInput
    ${this.labelProps ? `label="${this.sample.label}"` : ""}
    ${this.placeholderProps ? `placeholder="${this.sample.placeholder}"` : ""}
    ${
        this.errorMessageProps
            ? `:error-message="${this.sample.errorMessage}"`
            : ""
    }
    ${this.typeProps ? `:type="${this.sample.type}"` : ""}
    ${this.disabledProps ? "disabled" : ""}
    ${this.notRequiredProps ? "not-required" : ""}
    ${this.multiLineProps ? "multi-line" : ""}
    ${this.clearBtnProps ? ':clear-btn="false"' : ""}
/>`;
        },

        codeEmail() {
            return `
<BaseInput
    label=""
    placeholder=""
    v-model="${this.mail.value}"
    :is-valid.sync="${this.mail.isValid}"
    :rules="${JSON.stringify(this.mail.rules)}"
/>`;
        },

        codeCompare1() {
            return `
<BaseInput
    label=""
    placeholder=""
    type="password"
    v-model="${this.password.value}"
    :is-valid.sync="${this.password.isValid}"
    :rules="${JSON.stringify(this.password.rules)}"
    @onKeyup="this.confirmPasswordHandler"
/>`;
        },

        codeCompare2() {
            return `
<BaseInput
    label=""
    placeholder=""
    type="password"
    v-model="${this.confirmPassword.value}"
    :is-valid.sync="${this.confirmPassword.isValid}"
    :rules="${JSON.stringify(this.confirmPassword.rules)}"
    :error-message="${this.confirmPassword.errorMessage}"
    @onBlur="confirmPasswordHandler"
    @onKeyup="confirmPasswordHandler"
/>`;
        },

        codeNotRequired() {
            return `
<BaseInput
    label="Line"
    placeholder="Line"
    v-model="${this.line.value}"
    :is-valid.sync="${this.line.isValid}"
    not-required
/>`;
        },

        codeNumber() {
            return `
<BaseInput
    label=""
    placeholder=""
    type="number"
    v-model="${this.amount.value}"
    :is-valid.sync="${this.amount.isValid}"
    :rules="${JSON.stringify(this.amount.rules)}"
    :error-message="${this.amount.errorMessage}"
    @onBlur="amountHandler(${this.amount.value})"
    @onKeyup="amountHandler(${this.amount.value})"
/>`;
        },

        codeMultiLine() {
            return `
<BaseInput
    label=""
    placeholder=""
    v-model="${this.account.value}"
    :is-valid.sync="${this.account.isValid}"
    :rules="${JSON.stringify(this.account.rules)}"
    multi-line
/>`;
        },

        codeClearBtn() {
            return `
<BaseInput
    label="FB"
    v-model="${this.fb.value}"
    :is-valid.sync="${this.fb.isValid}"
    not-required
    :clear-btn="false"
/>`;
        },

        confirmPasswordHandlerFunciton() {
            return this.$options.methods.confirmPasswordHandler;
        }
    },
    methods: {
        confirmPasswordHandler() {
            if (this.password.value !== this.confirmPassword.value) {
                this.confirmPassword.errorMessage = "";
                this.confirmPassword.isValid = false;
            } else {
                this.confirmPassword.errorMessage = "";
                this.confirmPassword.isValid = true;
            }
        },
        amountHandler(amount) {
            if (amount < 100 || amount > 10000) {
                this.amount.errorMessage = "10010000";
                this.amount.isValid = false;
            } else {
                this.amount.errorMessage = "";
                this.amount.isValid = true;
            }
        },

        // 
        changeProps(target) {
            this.sample[target] = undefined;
        }
    }
};
</script>

<style scoped lang="scss">
.btn {
    margin: 0 0 20px 230px;
}
.base_input {
    max-width: 300px;
}
</style>
